
.banner_container{
    height: 500px;
}
.banner_image{
    height: 500px;
}
.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
}
.banner_button:hover{
    background-color: white;
    color: black;
}
.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width:360px;
    height: 80px;
}
.coloriser{
    height: 11rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);

}